@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';
@import '@fontsource/inter';
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@300;400;500;600;700;800&display=swap');

:root {
  --background-dark: #0a0c17;
  --background-light: #f8fafc;
  --card-dark: #121828;
  --card-light: #ffffff;
  --card-border-dark: #1d2a4a;
  --card-border-light: #e2e8f0;
  --text-primary-dark: #f1f5f9;
  --text-primary-light: #1e293b;
  --text-secondary-dark: #94a3b8;
  --text-secondary-light: #64748b;
  --text-tertiary-dark: #64748b;
  --text-tertiary-light: #94a3b8;
  --accent-blue: #2563eb;
  --accent-purple: #7c3aed;
  --accent-teal: #0ea5e9;
  --accent-green: #10b981;
  --accent-red: #ef4444;
  --gradient-primary: linear-gradient(135deg, var(--accent-blue), var(--accent-purple));
  --gradient-secondary: linear-gradient(135deg, var(--accent-teal), var(--accent-green));
  --glow-dark: 0 0 15px rgba(59, 130, 246, 0.4);
  --glow-light: 0 0 10px rgba(37, 99, 235, 0.1);
  --transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  
  /* Current theme variables (default to dark) */
  --background: var(--background-dark);
  --card-bg: var(--card-dark);
  --card-border: var(--card-border-dark);
  --text-primary: var(--text-primary-dark);
  --text-secondary: var(--text-secondary-dark);
  --text-tertiary: var(--text-tertiary-dark);
  --glow: var(--glow-dark);
  --header-bg: rgba(10, 12, 23, 0.95);
  --card-hover-bg: rgba(30, 41, 59, 0.6);
  --footer-bg: rgba(10, 12, 23, 0.95);
  --button-secondary-bg: rgba(18, 24, 40, 0.8);
  --button-secondary-hover: rgba(30, 41, 59, 0.8);
  --stats-bg: rgba(30, 41, 59, 0.6);
  --social-icon-bg: rgba(30, 41, 59, 0.8);
  --progress-bg: rgba(30, 41, 59, 0.8);
  --radial-gradient: radial-gradient(circle at 10% 20%, rgba(37, 99, 235, 0.1) 0%, transparent 20%),
                    radial-gradient(circle at 90% 80%, rgba(124, 58, 237, 0.1) 0%, transparent 20%);
  
  /* Swap page specific variables */
  --input-bg: #1e2235;
  --input-bg-light: #e5e7eb;
  --accent-yellow: #ffd166;
  --accent-green-swap: #06d6a0;
  --accent-red-swap: #ef476f;
  --accent-blue-swap: #4361ee;
}

/* App container styles */
.App {
  min-height: 100vh;
  background: var(--background);
  color: var(--text-primary);
  transition: var(--transition);
  background-image: var(--radial-gradient);
  background-attachment: fixed;
}

.App.light-mode {
  /* All light mode overrides - same structure as above but with light values */
  --background: var(--background-light);
  --card-bg: var(--card-light);
  --card-border: var(--card-border-light);
  --text-primary: var(--text-primary-light);
  --text-secondary: var(--text-secondary-light);
  --text-tertiary: var(--text-tertiary-light);
  --glow: var(--glow-light);
  --header-bg: rgba(248, 250, 252, 0.95);
  --card-hover-bg: rgba(226, 232, 240, 0.4);
  --footer-bg: rgba(248, 250, 252, 0.95);
  --button-secondary-bg: rgba(226, 232, 240, 0.6);
  --button-secondary-hover: rgba(203, 213, 225, 0.7);
  --stats-bg: rgba(226, 232, 240, 0.4);
  --social-icon-bg: rgba(226, 232, 240, 0.6);
  --progress-bg: rgba(226, 232, 240, 0.6);
  --radial-gradient: radial-gradient(circle at 10% 20%, rgba(37, 99, 235, 0.05) 0%, transparent 20%),
                    radial-gradient(circle at 90% 80%, rgba(124, 58, 237, 0.05) 0%, transparent 20%);
  
  /* Swap page light theme overrides */
  --input-bg: var(--input-bg-light);
  --accent-blue-swap: #2563eb;
  --accent-green-swap: #00c853;
  --accent-red-swap: #ef4444;
  --accent-yellow: #e6c200;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: var(--transition);
  background: var(--background);
  color: var(--text-primary);
  line-height: 1.6;
  background-image: var(--radial-gradient);
  background-attachment: fixed;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Orbitron', monospace;
  font-weight: 600;
}

/* Fix header and footer spacing */
main {
  padding-top: 7rem !important; /* 112px - accounts for header height */
  padding-bottom: 8rem !important; /* 128px - accounts for footer height + extra space */
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(30, 41, 59, 0.1);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: var(--gradient-primary);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #1d4ed8, #6d28d9);
}

/* Backdrop blur effect */
.backdrop-blur {
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

/* Gradient text */
.gradient-text {
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: var(--glow);
}

/* Smooth transitions */
.transition-all {
  transition: var(--transition);
}

/* Card hover effects */
.card-hover {
  transition: var(--transition);
}

.card-hover:hover {
  transform: translateY(-2px);
  box-shadow: var(--glow);
}

/* Form styles */
.form-card {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  transition: var(--transition);
}

.form-card:hover {
  box-shadow: var(--glow);
}

.form-input {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 0.75rem 1rem;
  color: var(--text-primary);
  transition: var(--transition);
  width: 100%;
}

.form-input:focus {
  outline: none;
  border-color: var(--accent-blue);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.form-input::placeholder {
  color: var(--text-tertiary);
}

.form-label {
  color: var(--text-secondary);
  font-weight: 500;
  margin-bottom: 0.5rem;
  display: block;
}

.form-help {
  color: var(--text-tertiary);
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

.form-error {
  color: var(--accent-red);
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

/* Button styles */
.btn {
  padding: 12px 24px;
  border-radius: 30px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 8px;
  border: none;
  text-decoration: none;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
  box-shadow: 0 4px 15px rgba(37, 99, 235, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(37, 99, 235, 0.4);
}

.btn-secondary {
  background: var(--button-secondary-bg);
  color: var(--text-primary);
  border: 1px solid var(--card-border);
}

.btn-secondary:hover {
  background: var(--button-secondary-hover);
  border-color: var(--accent-blue);
}

.btn-text {
  display: inline;
}

.btn-instant {
  background: var(--gradient-secondary);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  transition: var(--transition);
  cursor: pointer;
}

.btn-instant:hover {
  transform: translateY(-2px) scale(1.02);
  box-shadow: 0 0 20px rgba(14, 165, 233, 0.4);
}

.btn-instant.selected {
  background: var(--accent-green);
}

/* Info box */
.info-box {
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.1), rgba(14, 165, 233, 0.1));
  border: 1px solid rgba(37, 99, 235, 0.2);
  border-radius: 12px;
  padding: 1rem;
  margin-bottom: 2rem;
}

/* Like button styles */
.like-btn.liked {
  color: var(--accent-red) !important;
}

.like-btn.liked svg {
  fill: currentColor;
}

/* Container styles */
.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 1rem;
}

@media (min-width: 640px) {
  .container {
    padding: 0 1.5rem;
  }
}

@media (min-width: 1024px) {
  .container {
    padding: 0 2rem;
  }
}

/* Theme toggle rotation */
.theme-toggle:hover {
  transform: rotate(15deg);
}

/* Character counter */
.char-counter {
  color: var(--text-tertiary);
  font-size: 0.875rem;
  text-align: right;
  margin-top: 0.25rem;
}

.char-counter.warning {
  color: #f59e0b;
}

.char-counter.error {
  color: var(--accent-red);
}

/* Cost display */
.cost-item {
  background: var(--stats-bg);
  border-radius: 12px;
  padding: 1rem;
  text-align: center;
}

.cost-total {
  background: var(--gradient-primary);
  color: white;
  border-radius: 12px;
  padding: 1rem;
  text-align: center;
  font-weight: 600;
}

/* Enhanced form styles for Create Token page */
