/* Launch time preview */
.launch-time-preview {
  background: var(--button-secondary-bg);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 1rem;
  text-align: center;
  margin-top: 1rem;
  color: var(--text-secondary);
