    padding: 0.75rem 1rem;
    font-size: 0.9rem;
  }
}

@media (max-width: 640px) {
  .form-card {
    padding: 1rem;
  }
  
  .info-box {
    padding: 1rem;
  }
  
  .char-counter {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }
}

/* ManageToken page specific styles */
.warning-box {
  background: linear-gradient(135deg, rgba(239, 68, 68, 0.1), rgba(220, 38, 38, 0.1));
  border: 1px solid rgba(239, 68, 68, 0.2);
  border-radius: 12px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.btn-danger {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 0.875rem 1.5rem;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: var(--transition);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.3);
}

.btn-danger:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(239, 68, 68, 0.4);
}

.btn-danger:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.section-divider {
  height: 1px;
  background: var(--card-border);
  margin: 2rem 0;
  opacity: 0.5;
}

/* Enhanced form error styling */
.form-error {
  color: var(--accent-red);
  font-size: 0.875rem;
  margin-top: 0.25rem;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.2);
  border-radius: 8px;
  padding: 0.5rem;
}

.form-error::before {
  content: "⚠";
  color: var(--accent-red);
  font-weight: bold;
}

/* Token info header styling */
.token-info-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.token-logo-small {
  width: 3rem;
  height: 3rem;
  border-radius: 12px;
  background: var(--gradient-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.2rem;
  color: white;
  flex-shrink: 0;
}

.token-info {
  display: flex;
  flex-direction: column;
}

.token-name-large {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.token-symbol-large {
  color: var(--text-secondary);
  font-size: 0.95rem;
  font-weight: 500;
}

/* Enhanced button states */
.btn-primary:disabled,
.btn-secondary:disabled,
.btn-instant:disabled,
.btn-danger:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none !important;
  box-shadow: none !important;
}

/* Loading states */
.btn-primary:disabled::after,
.btn-secondary:disabled::after,
.btn-instant:disabled::after,
.btn-danger:disabled::after {
  content: "";
  width: 16px;
  height: 16px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-left: 0.5rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Enhanced info box styling */
.info-box {
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.1), rgba(14, 165, 233, 0.1));
  border: 1px solid rgba(37, 99, 235, 0.2);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  transition: var(--transition);
}

.info-box:hover {
  border-color: rgba(37, 99, 235, 0.3);
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.15), rgba(14, 165, 233, 0.15));
}

/* Responsive improvements for ManageToken */
@media (max-width: 768px) {
  .token-info-header {
    flex-direction: column;
    text-align: center;
    gap: 0.75rem;
  }
  
  .warning-box,
  .info-box {
    padding: 1rem;
  }
  
  .section-divider {
    margin: 1.5rem 0;
  }
  
  .btn-danger,
  .btn-primary {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 640px) {
  .token-name-large {
    font-size: 1.25rem;
  }
  
  .warning-box,
  .info-box {
    padding: 0.75rem;
  }
  
  .form-error {
    padding: 0.375rem;
    font-size: 0.8rem;
  }
} 

/* Wallet Management Page Specific Styles */
.wallet-container {
  max-width: 800px;
  margin: 0 auto;
}

.wallet-card {
  background: var(--card-bg);
  border: 1px solid var(--card-border);
  border-radius: 16px;
  padding: 25px;
  margin-bottom: 30px;
  transition: var(--transition);
}

.wallet-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.wallet-status {
  display: flex;
  align-items: center;
  gap: 10px;
  color: var(--text-primary);
  font-weight: 600;
}

.status-indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: var(--accent-green);
  box-shadow: 0 0 8px rgba(16, 185, 129, 0.4);
}

.wallet-address {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 20px;
  padding: 12px;
  background: var(--stats-bg);
  border-radius: 12px;
  border: 1px solid var(--card-border);
}

.wallet-actions {
  display: flex;
  gap: 10px;
}

.copy-btn {
  background: var(--button-secondary-bg);
  color: var(--text-primary);
  border: 1px solid var(--card-border);
  border-radius: 10px;
  padding: 8px 12px;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 0.9rem;
  font-weight: 500;
  transition: var(--transition);
}

.copy-btn:hover {
  background: var(--button-secondary-hover);
  border-color: var(--accent-blue);
  color: var(--accent-blue);
}

.disconnect-btn {
  background: var(--button-secondary-bg);
  color: var(--text-primary);
  border: 1px solid var(--card-border);
  border-radius: 10px;
  padding: 8px 12px;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 0.9rem;
  font-weight: 500;
  transition: var(--transition);
}

.disconnect-btn:hover {
  background: var(--button-secondary-hover);
  border-color: var(--accent-red);
  color: var(--accent-red);
}

.balance-section {
  background: var(--stats-bg);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 25px;
